.app {
  display: flex;
}

.app-contents {
  display: flex;
  position: relative;
  contain: layout size style;
}

.shadow-split-widget-sidebar {
  flex: auto;
  flex-basis: 1231px;
}


.shadow-split-widget {
  display: flex;
  overflow: hidden;
}

.shadow-split-widget.hbox > .shadow-split-widget-sidebar {
  border: 0;
  border-right: 1px solid var(--divider-color);
}

.shadow-split-widget-contents {
  display: flex;
  position: relative;
  flex-direction: column;
  contain: layout size style;
}

.shadow-split-widget-sidebar {
  flex: none;
  flex-basis: 300px;
}

.shadow-split-widget-main {
  flex: auto;
}

.gutter {
  cursor: ew-resize;
  border-left: var(--divider-border);
  margin-left: -1px;
  margin-right: -4px;
  z-index: 9999;
}


.details-data {
  padding: 4px;
  background-color:var(--network-bg);
  overflow: scroll;
}

.react-json-view svg {
    height: 0.9em !important;
}

.network-empty {
  display: flex;
  align-content: center;
  align-items: center;
  flex: 1 1;
  background-color: var(--network-bg);
}

.network-empty .content {
  text-align: center;
  font-size: 14px;
  line-height: 28px;
  margin: auto;
  color: var(--network-color-text);
}


.network-list {
  overflow: hidden;
  background-color:var(--network-bg);
}

.data-grid {
  position: relative;
  line-height: 120%;
  border: none;
  flex: auto;
}

.data-grid table {
  table-layout: fixed;
  border-spacing: 0;
  border-collapse: separate;
  height: 100%;
  width: 100%;
}

.data-grid th > div {
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}

.data-grid th {
  text-align: left;
  background-color: var(--toolbar-bg-color);
  color: var(--toolbar-color);
  border-bottom: var(--divider-border);
  font-weight: normal;
  vertical-align: middle;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  line-height: 18px;
  height: 18px;
  padding: 1px 4px;
}

.data-grid .data .data-row.odd {
  background-color: var(--network-row-odd);
}

.data-grid .data .data-row:hover {
  background-color: var(--network-row-hover);
}

.data-grid .data .data-row.selected {
  color: var(--selection-fg-color);
  background-color: var(--selection-bg-color);
}

.data-grid .data .data-row.error {
  color:rgb(216, 0, 0);
}

.data-grid .data-row {
  color: var(--toolbar-color);
  cursor: pointer;
  height: 21px;
  vertical-align: middle;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  line-height: 18px;
  padding: 1px 4px;
}

.data-grid .data-row svg {
  vertical-align: middle;
  fill: var(--network-row-icon-fill);
  margin-right: 4px;
  width: 10px;
}

.data-grid .data-container {
  top: 27px;
  bottom: 0;
  overflow-y: overlay;
  -webkit-transform: translateZ(0);
          transform: translateZ(0);
  position: absolute;
  left: 0;
  right: 0;
  overflow-x: hidden;
}

.data-grid .data {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  border-top: 0 none transparent;
  table-layout: fixed;
  height: auto;
}

.header-container {
  height: 27px;
  background-color: var(--toolbar-bg-color);
  position: absolute;
  left: 0;
  right: 0;
  overflow-x: hidden;
}


.toolbar {
  background-color: var(--toolbar-bg-color);
  border-bottom: var(--divider-border);
  flex: none;
  padding: 0 2px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.toolbar-shadow {
  position: relative;
  white-space: nowrap;
  height: 26px;
  overflow: hidden;
  z-index: 12;
  display: flex;
  flex: none;
  align-items: center;
}

.toolbar-item {
  color: var(--toolbar-color);
  position: relative;
  display: flex;
  background-color: transparent;
  flex: none;
  align-items: center;
  justify-content: center;
  padding: 0;
  height: 26px;
  border: none;
  white-space: pre;
}

.toolbar-item.checkbox {
  padding: 0 5px 0 2px;
}

.toolbar-item.checkbox:hover {
  color: var(--toolbar-color-hover);
}

.toolbar-item.text {
  margin: 0 3px;
}

.toolbar-item.text input {
  padding-left: 3px;
  width: 163px;
  height: 18px;
  line-height: 20px;
  display: inline-block;
  color: var(--toolbar-input-text);
  background: var(--toolbar-input-bg);
  overflow-x: visible;
  white-space: nowrap;
  border: none;
}

.toolbar-item.text input:hover {
  box-shadow: var(--focus-ring-inactive-shadow);
}

.toolbar-item.text input:focus {
  box-shadow: var(--focus-ring-active-shadow);
}

:focus {
  outline-width: 0;
}

.toolbar-button {
  white-space: nowrap;
  overflow: hidden;
  min-width: 28px;
  background: transparent;
  border-radius: 0;
}

.toolbar-divider {
  background-color: #ccc;
  width: 1px;
  margin: 5px 4px;
  height: 16px;
}

.toolbar-button svg {
  fill: var(--toolbar-color);
}

.toolbar-button:hover:not(:active) svg {
  fill: var(--toolbar-color-hover);
}

.toolbar-button.open svg {
  fill: var(--accent-color) !important;
}

.toolbar-button.enabled svg {
  fill: var(--enabled-color) !important;
}

html {
  height: 100%;
  overflow: hidden;
}

body {
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;
  position: relative;
  overflow: hidden;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 12px;
  -moz-tab-size: 4;
       tab-size: 4;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  box-sizing: border-box;
  min-width: 0;
  min-height: 0;
}

#root {
  margin-right: 0px;
  margin-bottom: 0px;
  background-color: white;
  overflow: hidden;
  position: absolute !important;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  display: flex;
  flex-direction: column !important;
  flex: auto;
  contain: style;
}

:root {
  --accent-color: #1a73e8;
  --toolbar-bg-color: #f3f3f3; 
  --toolbar-color: #5a5a5a;
  --toolbar-color-hover: #333;
  --divider-border: 1px solid #d0d0d0;
  --divider-color: #d0d0d0;
  --selection-bg-color: var(--accent-color);
  --selection-fg-color: white;
  --toolbar-input-text: #000;
  --toolbar-input-bg: #fff;
  --focus-ring-inactive-shadow: 0 0 0 1px #e0e0e0;
  --focus-ring-active-shadow: 0 0 0 1px var(--accent-color);
  --enabled-color: #d80000;
  --network-color-text: #777;
  --network-bg: #fff;
  --network-row-odd: #f5f5f5;
  --network-row-hover: #f1f6fd;
  --network-row-icon-fill: rgba(0, 0, 0, 0.2);

}

@media (prefers-color-scheme: dark) {
  :root {
    --accent-color: #0e639c;
    --toolbar-bg-color: #333333;
    --toolbar-color: #a5a5a5;
    --toolbar-color-hover: #ccc;
    --divider-border: 1px solid #3d3d3d;
    --toolbar-input-text: #bdc6cf;
    --toolbar-input-bg: #242424;
    --focus-ring-inactive-shadow: 0 0 0 1px #5a5a5a;
    --focus-ring-active-shadow: 0 0 0 1px var(--accent-color);
    --enabled-color: #ff4d4d;
    --network-color-text: #888888;
    --network-bg: #242424;
    --network-row-odd: #292929;
    --network-row-hover: #14253fb3;
    --network-row-icon-fill: rgba(255, 255, 255, 0.8);
  }

  input[type=checkbox] {
    -webkit-filter: invert(80%);
            filter: invert(80%);
  }

  a {
    color: #ababab;
  }
}

.flex-auto {
  flex: auto;
}

.vbox {
  display: flex;
  flex-direction: column !important;
  position: relative;
}

.hbox {
  display: flex;
  flex-direction: row !important;
  position: relative;
}

.widget {
  position: relative;
  flex: auto;
  contain: style;
}

